.contact-form {
  font-family: "Montserrat", sans-serif;
  background-image: linear-gradient(
    to bottom,
    #080b1a 0%,
    #080b1a 7%,
    #070b1b 23%,
    #0c1a63 100%
  );
  background-size: contain;
  background-repeat: no-repeat;
  background-position: bottom;
  color: #e0dcdc;
  padding: 45px 0;
  text-align: center;
  min-height: 100vh;
  h2 {
    font-family: "Montserrat-bold";
    margin-bottom: 25px;
  }
  form {
    display: flex;
    flex-direction: column;
    margin-top: 50px;
    padding: 0 20px;
    input {
      margin-bottom: 25px;
      padding: 10px;
      border: none;
      background-color: transparent;
      border-bottom: 1px solid #ffffff;
      outline: none;
      color: #ffffff;
      &::placeholder {
        color: #ffffff;
      }
    }
    button {
      padding: 10px;
      background-color: #ffffff;
      color: #080b1a;
      border: none;
      text-transform: uppercase;
      cursor: pointer;
      transition: 0.5s;
      width: 140px;
      margin: auto;
      border-radius: 100px;
      outline: none;
      font-family: "Montserrat-bold";
      &:hover {
        background-color: #080b1a;
        color: #ffffff;
      }
    }
  }
  .video {
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
    flex-direction: column;
    @media (max-width: 480px) {
      padding: 0px;
    }

    p {
      margin-right: auto;
      font-family: "Montserrat-Medium";
      margin-bottom: 0;
    }
    iframe {
      border-radius: 15px;
      margin-left: 20px;
      margin-top: 24px;
      @media (max-width: 480px) {
        margin-left: 0;
        width: 100%;
        margin-bottom: 20px;
      }
    }
  }
}
.payment {
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.section-1 {
  padding: 40px 0;
  .content {
    display: flex;
    justify-content: center;
    align-items: center;
    @media (max-width: 480px) {
      flex-direction: column-reverse;
    }
    p {
      font-family: "Montserrat-Medium";
      text-align: right;
      font-size: 20px;
      @media (max-width: 480px) {
        text-align: center;
      }
    }
    .imag-box {
      // padding: 0 20px;
      width: 40%;

      // margin: auto;
      border-radius: 50%;
      shape-outside: circle(50%);
    }
  }
}
.section-2 {
  padding: 0 0 40px;
  @media (max-width: 480px) {
    text-align: center;
  }

  .image-box {
    width: 200px;
    height: 190px;
    @media (max-width: 480px) {
      margin: auto;
    }
  }
}
footer {
  background-image: linear-gradient(
    to left,
    #080b1a 0%,
    #080b1a 7%,
    #070b1b 23%,
    #070f36 100%
  );
  background-size: contain;
  background-repeat: no-repeat;
  background-position: bottom;
  color: #e0dcdc;
  padding: 15px 0;
  text-align: center;
  p {
    margin-bottom: 0;
  }
}
